import { string, object } from 'yup'

const FIELD_REQUIRED = '   '
const LENGTH_VIOLATION = '    3  20 '
const WHITESPACES_VIOLATION = '      '

export const validationSchema = object({
    name: string()
        .required(FIELD_REQUIRED)
        .min(3, LENGTH_VIOLATION)
        .max(20, LENGTH_VIOLATION)
        .trim(WHITESPACES_VIOLATION)
        .test(
            '  ',
            '    ',
            (value) => !!value?.match(/^[- ]+$/gi)
        ),
    login: string()
        .trim(WHITESPACES_VIOLATION)
        .required(FIELD_REQUIRED)
        .min(3, LENGTH_VIOLATION)
        .max(20, LENGTH_VIOLATION)
        .test(
            '  ',
            '  ',
            (value) => !!value?.match(/^[a-z]+$/gi)
        ),
    password: string()
        .trim(WHITESPACES_VIOLATION)
        .required(FIELD_REQUIRED)
        .min(3, LENGTH_VIOLATION)
        .max(20, LENGTH_VIOLATION)
        .test(
            '    ',
            '  ,    ',
            (value) => !!value?.match(/^[a-z\d_]+$/gi)
        ),
})
